<template lang="html">
<div class="page">
  <cube-scroll ref="scroll" :options="{scrollbar:{fade:true}}">
    <div class="title-bar">
      <h2>{{obj.Title}}</h2>
      <p>{{obj.Summary}}</p>
    </div>
    <div class="flex bg-white line-top p-md">
      <avatar :src="obj.Avatar" class="mr"></avatar>
      <div class="f-body">
        <div class="fs-sm tc-grey">{{obj.NickName}}</div>
        <div class="desc flex jc-sb">
          <p class="left">部门：财务部</p>
          <span>{{ obj.UpdateTime }}</span>
        </div>
      </div>
    </div>
    <tit has-bg class="mt-md">填写信息</tit>
    <div class="p-md bg-white">
      <cube-textarea v-model="content" placeholder="输入您要处理的内容" :maxlength="50"></cube-textarea>
      <h4 class="title mt">照片</h4>
      <div class="upload-row flex">
        <btn icon="icon-recordfill" type="primary" ></btn>
        <div class="f-body pl">
          <el-row :gutter="10" >
            <el-col :span="8" v-for="(item,index) in List" :key="index+Math.random()" v-if="index<3">
              <g-item :thum-src="item.CoverImage" img-height="60" href="/"></g-item>
            </el-col>
          </el-row>
        </div>
      </div>
      <h4 class="title mt">视频</h4>
      <div class="upload-row flex">
        <btn icon="icon-recordfill" type="warn" ></btn>
        <div class="f-body pl">
          <el-row :gutter="10" >
            <el-col :span="8" v-for="(item,index) in List" :key="index+Math.random()" v-if="index<3">
              <g-item :thum-src="item.CoverImage" img-height="60" is-video href="/"></g-item>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
    <div class="btn-wrap">
      <btn type="primary" size="lg" block link="/">办结，提交办理信息</btn>
      <btn type="warn" size="lg" block link="/">未办结，提交办理信息</btn>
      <btn block size="lg" link="/home/zybl/2">转移办理</btn>

    </div>

  </cube-scroll>

</div>
</template>

<script>
export default {

  data() {
    return {
      List: [],
      obj:{},
      content:''
    }
  },
  created () {
    this.getListData()
  },
  methods : {
    getListData() {
      this.id = this.$route.params.id
      this.axios.get('/static/data.json').then((res) => {
        this.List = res.data.List
        this.obj = res.data.List[this.id]
      })
    },


  },
}
</script>

<style lang="stylus">
@import "assets/stylus/base"
.upload-row
  .btn
    height: 60px;
    .icon
      font-size: 28px
</style>



